import React from 'react';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import Owner from './Family/Owner.jsx';
import Subheader from 'material-ui/Subheader';
import RaisedButton from 'material-ui/RaisedButton';
import Follow from 'material-ui/svg-icons/social/mood';
import Star from 'material-ui/svg-icons/action/stars';


class Main extends React.Component {
  constructor() {
    super();
    this.state = {
      counter: 0,
      counterOfOwned: 0
    }
  }

  render() {
    let demoStyle = {
      container: {
        width: '330px',
        margin: '0 auto'
      },
      subHeader: {
        textAlign: 'center'
      }
    };
    return (
      <div style={demoStyle.container}>

        <MuiThemeProvider muiTheme={getMuiTheme()}>
          <div>
            <Owner />
            <Subheader style={demoStyle.subHeader}>
              <RaisedButton
                label="Star it"
                linkButton={true}
                href="https://github.com/ozluy/react-owner-owned-bindings"
                secondary={true}
                icon={<Star />}
              />
              <RaisedButton
                label="FOLLOW ME"
                linkButton={true}
                backgroundColor="#a4c639"
                href="https://github.com/ozluy"
                icon={<Follow />}
              />
            </Subheader>
          </div>
        </MuiThemeProvider>
      </div>
    );
  }


}

module.exports = Main;



