import React from 'react';
import Paper from 'material-ui/Paper';
import RaisedButton from 'material-ui/RaisedButton';
import Subheader from 'material-ui/Subheader';
import ActionPets from 'material-ui/svg-icons/action/pets';

class Owned extends React.Component {
  constructor() {
    super();
    this.state = {
      ownCounter: 0
    }
  }

  render() {
    let demoStyle = {
      ownedShell: {
        width: '250px',
        backgroundColor: '#ccc',
        padding: '15px',
        margin:'0 auto'
      },
      resultText: {
        color: '#00aa00',
        padding: '15px'
      },
      counterText:{
        padding: '10px',
        display:'block'
      },
      subHeader:{
        textAlign: 'left'
      }
    };
    return (
      <Paper style={demoStyle.ownedShell}>
        <Subheader style={demoStyle.subHeader}><ActionPets/> Owned </Subheader>
          <RaisedButton onClick={this._increaseOwnerCount.bind(this)}  secondary={true} label="Increase Owner Count"/>
          <div style={demoStyle.counterText}>Counter From Owner: {this.props.counterOwned}</div>
        <hr />
          <RaisedButton onClick={this._increaseOwnCount.bind(this)}  primary={true} label="Increase Own Count"/>
          <div style={demoStyle.counterText}>Own Counter: {this.state.ownCounter}</div>
      </Paper>
    );
  }
  _increaseOwnerCount(){
    this.props.countUp();
  }
  _increaseOwnCount(){
    this.setState({
      ownCounter:this.state.ownCounter + 1
    });
  }

}
Owned.props = {
  countUp: React.PropTypes.func,
  counterOwned: React.PropTypes.number
}
module.exports = Owned;



