import React from 'react';
import Paper from 'material-ui/Paper';
import Owned from './Owned.jsx'
import RaisedButton from 'material-ui/RaisedButton';
import Subheader from 'material-ui/Subheader';
import ActionPets from 'material-ui/svg-icons/action/pets';

import injectTapEventPlugin from 'react-tap-event-plugin';
injectTapEventPlugin();


class Owner extends React.Component {
  constructor() {
    super();
    this.state = {
      counter: 0,
      counterOfOwned: 0
    }
  }

  render() {
    const demoStyle= {
      owner: {
        width: '330px',
        backgroundColor: '#cc0000',
        padding: '15px',
        textAlign:'center'
      },
      counterText:{
        padding: '10px',
        display:'block',
        color:'#fff'
      },
      subHeader:{
        textAlign: 'left',
        color:'#fff'
      }

    };
    return (
      <Paper style={demoStyle.owner}>
        <Subheader style={demoStyle.subHeader}><ActionPets color="#fff" />Owner</Subheader>
        <RaisedButton
          className="floatRight"
          onTouchTap={this._increaseNumberOfOwned.bind(this)}
          label="Increase Owned Count"
        />
        <div style={demoStyle.counterText}>Counter From Owned: {this.state.counter}</div>

        <hr />
        <Owned
          countUp={this._increaseNumberOfOwner.bind(this)}
          counterOwned={this.state.counterOfOwned}
        />

      </Paper>
    );
  }

  _increaseNumberOfOwner() {
    this.setState({
      counter: this.state.counter + 1
    })
  }

  _increaseNumberOfOwned() {
    this.setState({
      counterOfOwned: this.state.counterOfOwned + 1
    })
  }
}

Owned.states = {
  counter: React.PropTypes.number,
  counterOfOwned: React.PropTypes.number
}
export default Owner;




